.header__top {
  background-color: #4d4d4d;
  min-height: 43px;
  display: flex;
  align-items: center;
}

.header__top-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__top-action {
  display: flex;
  align-items: center;

  svg {
    fill: $white;
  }
}

.header__top-btn,
.header__top-link {
  display: flex;
  align-items: center;

  font-size: 12px;
  color: $white;
}

.header__top-btn {
  margin-right: 28px;

  svg {
    margin-right: 10px;
  }
}

.header__top-link {
  padding: 8.5px 34px;
  background-color: $yellow;
  transition: background-color .2s linear;

  svg {
    margin-right: 5px;
  }

  &:hover {
    background-color: darken($yellow, 5%);
  }
}

.header__middle-inner {
  padding: 15px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo-wrapper {
  margin-right: 39px;
  display: flex;
  align-items: center;
}

.header__logo-text {
  padding-left: 10px;
  border-left: 1px solid $white;

  max-width: 160px;
  font-size: 14px;
}

.header__form-popup-fade {
  margin-right: 38px;
  display: flex;
  flex-grow: 1;
}

.header__form-popup {
  flex-grow: 1;
}

.header__form {
  position: relative;
}

.header__form-close {
  display: none;
}

.header__form-input {
  width: 100%;

  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  color: $black;

  padding: 14.5px 60px 14.5px 26px;
  background-color: $white;
  border: 1px solid #e2e2e2;
  border-radius: 0;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  &::placeholder {
    color: #adadad;
  }
}

.header__form-btn {
  padding: 0 11.5px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $yellow;
  transition: background-color .2s linear;

  &:hover {
    background-color: darken($yellow, 5%);
  }
}

.header__action {
  margin-right: 17px;
  display: flex;
  align-items: center;
}

.header__action-item {
  position: relative;
}

.header__action-item--search {
  display: none;
}

.header__action-item + .header__action-item {
  margin-left: 30px;
}

.header__action-link {
  display: flex;
  align-items: center;
  position: relative;
  font-size: 14px;

  &--phone {
    font-size: 16px;
    font-weight: 700;
  }
}

.header__action-icon__phone {
  margin-right: 12px;
}

.header__action-icon__dropdown {
  fill: $white;
  margin-left: 8px;
  transition: transform .2s linear;

  &.rotate {
    transform: rotate(180deg);
  }
}

.header__action-icon__bag {
  margin-right: 8px;
}

.header__action-bag__number {
  min-width: 18px;
  height: 18px;
  background-color: $yellow;
  border-radius: 50%;
  font-size: 14px;
  line-height: 100%;
  font-weight: 400;
  color: $black;
  position: absolute;
  top: -8px;
  left: 15px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.header__bottom {
  height: 65px;
  background-color: $yellow;
}

.header__bottom-inner {
  display: flex;
  align-items: center;
}

.header__bottom-discount {
  background: url('../img/bg.jpg') no-repeat center;
  background-size: cover;

  width: 191px;
  height: 65px;
  display: flex;
  align-items: center;
  justify-content: center;

  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-size: 25px;
  color: $white;
}

.header__action-icon__scale {
  fill: $white;
}

.header__action-icon__favourite {
  fill: $white;
}

.header__bottom-additional {
  padding: 20px 10px 10px;
  width: 100%;
  display: none;
  color: #3c3c3c;

  svg {
    margin-right: 10px;
    width: 15%;
    fill: #3c3c3c;
  }

  a {
    display: flex;
    align-items: center;
    font-weight: 700;
  }

  li + li {
    margin-top: 15px;
  }
}

@media (max-width: 1599px) {
  .header__bottom-discount {
    font-size: 22px;
    width: 160px;
  }
}

@media (max-width: 1399px) {
  .header__bottom-discount {
    font-size: 20px;
    width: 130px;
  }

  .header__form-input {
    padding: 12px 50px 12px 20px;
  }

  .header__form-btn {
    padding: 0 10px;

    svg {
      width: 20px;
      height: 20px;
    }
  }

  .header__action {
    margin-right: 10px;
  }

  .header__action-item + .header__action-item {
    margin-left: 20px;
  }

  .header__form-popup-fade {
    margin-right: 30px;
  }

  .header__logo-wrapper {
    margin-right: 15px;
  }
}

@media (max-width: 1199px) {
  .header__bottom-discount {
    font-size: 18px;
    width: 110px;
  }

  .header__action-link--phone span {
    display: none;
  }

  .header__action-icon__dropdown {
    display: none;
  }

  .header__action-icon__phone {
    margin: 0;
    width: 20px;
    height: 20px;
  }

  .header__action-icon__scale {
    width: 29px;
    height: 22px;
  }

  .header__action-icon__favourite {
    width: 22px;
    height: 19px;
  }

  .header__action-icon__bag {
    width: 20px;
    height: 22px;
    margin: 0;
  }

  .header__action-bag__number {
    min-width: 15px;
    height: 15px;
    font-size: 13px;
    left: 11px;
    top: -5px;
  }

  .header__action-link-bag__text {
    display: none;
  }
}

@media (max-width: 1099px) {
  .header__bottom {
    height: 60px;
  }

  .header__bottom-discount {
    height: 60px;
    font-size: 17px;
  }
}

@media (max-width: 991px) {
  .header__top {
    display: none;
  }

  .header__logo-text {
    font-size: 13px;
  }

  .header__action {
    margin: 0 10px 0 auto;
  }

  .header__action-item--scale,
  .header__action-item--fav {
    display: none;
  }

  .header__middle {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 29;
    width: 100%;
    background-color: #3c3c3c;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);
  }

  .header__middle-inner {
    padding: 5px 0;
    justify-content: flex-start;
  }

  .header__bottom {
    padding: 15px 0;
    display: none;
    width: auto;
    height: auto;
    max-height: calc(100vh - 69px);
    background-color: $white;
    position: fixed;
    top: 69px;
    left: 0;
    z-index: 19;
    overflow-y: auto;
    overflow-x: hidden;

    .container {
      max-width: none;
      padding: 0;
    }
  }

  .header__bottom-inner {
    flex-direction: column;
    align-items: flex-start;
  }

  .header__bottom-discount {
    padding: 13px 20px 13px 25px;
    height: auto;
    width: auto;
    background: none;
    font-size: 22px;
    color: #3c3c3c;
  }

  .header__form-input {
    padding: 10px 35px 10px 10px;
    font-size: 14px;
  }

  .header__form-btn {
    padding: 0;
    width: 30px;
    height: 39px;
  }

  .header__bottom-additional {
    display: block;
  }

  .header__form-popup-fade {
    margin: 0;
    display: none;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .7);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    overflow-y: auto;
    overflow-x: hidden;
  }

  .header__form-popup-fade.show {
    display: block;
  }

  .header__form-popup {
    margin: 20px auto;
    background-color: #3c3c3c;
    position: relative;
    padding: 80px 30px 60px;
    width: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .header__form {
    width: 100%;
  }

  .header__form-close {
    display: block;
    position: absolute;
    top: 15px;
    right: 15px;
    width: 20px;
    height: 20px;
    background: 0 0;
    border: 0;
    cursor: pointer;
    padding: 0;

    &:before,
    &:after {
      content: '';
      height: 20px;
      width: 2px;
      position: absolute;
      top: 0;
      left: 50%;
      margin-left: -1px;
      background-color: #ffc62b;
    }

    &:before {
      transform: rotate(45deg);
    }

    &:after {
      transform: rotate(-45deg);
    }
  }

  .header__action-item--search {
    display: block;

    svg {
      fill: $yellow;
    }
  }
}

@media (max-width: 767px) {
  .header__logo-text {
    display: none;
  }

  .header__action {
    margin: 0 0 0 auto;
  }

  .header__form-popup-fade {
    margin-right: 25px;
  }

  .header__logo-img {
    width: 160px;
    height: 53px;
  }

  .header__bottom {
    top: 63px;
    max-height: calc(100vh - 63px);
  }
}

@media (max-width: 575px) {

  .header__logo-img {
    width: 136px;
    height: 45px;
  }

  .header__logo-wrapper {
    margin: 0;
  }

  .header__bottom {
    width: 100%;
    top: 50px;
    max-height: calc(100vh - 50px);
  }

  .header__middle-inner {
    padding: 3px 0 2px;
  }
}

@media (max-width: 399px) {
  .header__action-item+.header__action-item {
    margin-left: 15px;
  }
}